.zolo-popup-ai {
    // loader css start
    .zolo-ai-loader {
        --uib-size: 100%;
        --uib-color: var(--zoloblocks-brand-color);
        --uib-speed: 1.4s;
        --uib-stroke: 2px;
        --uib-bg-opacity: 0.1;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        height: var(--uib-stroke);
        width: var(--uib-size);
        overflow: hidden;
        transform: translate3d(0, 0, 0);
        bottom: -2px;
        left: 0;
        right: 0;
        box-sizing: border-box;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: transparent;
            opacity: var(--uib-bg-opacity);
            transition: background-color 0.3s ease;
        }

        &::after {
            content: '';
            height: 100%;
            width: 100%;
            border-radius: calc(var(--uib-stroke) / 2);
            animation: zoloailoader var(--uib-speed) ease-in-out infinite;
            transform: translateX(-100%);
            background-color: var(--uib-color);
            transition: background-color 0.3s ease;
        }
    }

    @keyframes zoloailoader {
        0% {
            transform: translateX(-100%);
        }
        100% {
            transform: translateX(100%);
        }
    }

    // loader css end

    &.components-modal__frame {
        max-width: 600px;
        width: calc(100% - 64px);
        // box-shadow:
        //     rgba(0, 0, 0, 0.2) 0px 11px 15px -7px,
        //     rgba(0, 0, 0, 0.14) 0px 24px 38px 3px,
        //     rgba(0, 0, 0, 0.12) 0px 9px 46px 8px;
        border-radius: 6px;
        max-height: 76vh;
    }

    .components-modal__content {
        /* width */
        &::-webkit-scrollbar {
            width: 5px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            background: #f1f1f1;
            border-radius: 6px;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #c4c4c4;
            border-radius: 6px;
        }
    }

    .components-modal__content {
        padding: 0;
    }

    .zolo-popup-header {
        padding: 18px 24px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid rgba(62, 66, 79, 0.11);
        position: relative;

        .zolo-ai-badge {
            background: #3f8efc24;
            line-height: 1;
            padding: 6px 8px;
            color: #3f8efc;
            font-size: 10px;
            border-radius: 30px;
            font-weight: 700;
            text-transform: uppercase;
        }
        .zolo-popup-header_right {
            display: flex;
            align-items: center;
            gap: 6px;
            .zolo-head-upgrade-btn {
                border: 0;
                display: flex;
                align-items: center;
                flex-direction: row-reverse;
                gap: 4px;
                padding: 9px 12px;
                font-size: 13px;
                line-height: 1;
                border-radius: 30px;
                cursor: pointer;
                color: var(--zoloblocks-brand-color);
                background-color: #e5f0ff;
                transition: all 0.3s ease;
                text-decoration: none;
                font-weight: 600;
                svg {
                    width: 18px;
                    height: 18px;
                    display: block;
                    fill: currentColor;
                }
                &:hover,
                &:focus {
                    border-color: var(--zoloblocks-brand-color);
                    box-shadow: 0 0 0 2px var(--zoloblocks-brand-color);
                }
            }
        }
    }

    .zolo-popup-header__logo {
        display: flex;
        align-items: center;
        gap: 8px;
        padding: 6px;
        svg {
            width: 28px;
            height: 28px;
            display: block;
        }
        .zolo-ai-logo-text {
            margin: 0;
            font-size: 16px;
            color: #000000;
            font-weight: 600;
        }
    }

    .zolo-head-close-button {
        background: transparent;
        border: 0;
        cursor: pointer;
        padding: 6px;
        svg {
            width: 24px;
            height: 24px;
            display: block;
            color: #6e7178;
        }
        &:hover {
            svg {
                color: #000000;
            }
        }
    }

    .zolo-popup__content_wrap {
        padding: 30px;
        textarea {
            box-shadow: 0 0 0 transparent;
            border-radius: 6px;
            border: 2px solid rgba(70, 70, 72, 0.71);
            background-color: #fff;
            color: #202225;
            padding: 14px;
            font-size: 16px;
            font-weight: 400;
            min-height: 130px;
            &::placeholder {
                color: #6e7178;
            }
            &:focus {
                border-color: var(--zoloblocks-brand-color);
                box-shadow: 0 0 0 1px var(--zoloblocks-brand-color);
            }
        }
    }

    .zolo-popup-response-button {
        display: flex;
        align-items: center;
        gap: 10px;
        flex-wrap: wrap;
        padding: 30px 0;
        .components-button {
            &:hover,
            &:focus {
                color: var(--zoloblocks-brand-color);
                background: transparent;
                border-color: var(--zoloblocks-brand-color);
                box-shadow: 0 0 0 1px var(--zoloblocks-brand-color);
            }
        }
    }

    .components-button {
        border-radius: 30px;
        padding: 20px 14px;
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        gap: 8px;
        background: transparent;
        border: 1px solid rgba(42, 42, 52, 0.18);
        color: #72727a;
        font-size: 14px;
        font-weight: 400;
        box-shadow: none;
        outline: none;
        &.longer,
        &.shorter {
            svg {
                width: 25px;
                height: 25px;
            }
        }
        svg {
            outline: none;
            display: block;
            width: 20px;
            height: 20px;
            path {
                fill: currentColor;
            }
        }
        &:hover,
        &:focus {
            color: var(--zoloblocks-brand-color);
            background: transparent;
            border-color: var(--zoloblocks-brand-color);
            box-shadow: 0 0 0 1px var(--zoloblocks-brand-color);
        }
    }

    .zolo-popup-response-lang {
        display: flex;
        align-items: center;
        gap: 15px;
        flex-direction: row-reverse;
        .components-input-control__label {
            font-size: 14px !important;
            text-transform: capitalize !important;
            color: #3e3e43;
        }
        .components-base-control {
            width: 100%;
            box-sizing: border-box;
            select {
                padding: 8px 14px;
                height: auto;
                color: #3e3e43;
                font-size: 14px;
                font-weight: 400;
            }
            .components-input-control__suffix {
                height: auto;
                > div {
                    padding-inline-end: 14px;
                }
                svg {
                    width: 24px;
                    height: 24px;
                    display: block;
                }
            }
            .components-input-control__backdrop {
                border-radius: 6px;
                border: 1px solid rgba(42, 42, 52, 0.18);
                box-shadow: none;
            }
        }
    }

    .zolo-popup-footer {
        padding: 30px;
        border-top: 1px solid rgba(62, 66, 79, 0.11);

        .zolo-popup-footer-actions {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .components-button {
                font-weight: 500;
                padding: 20px 14px;
                color: #43434a;
                &:hover {
                    color: var(--zoloblocks-brand-color);
                }
            }
            .zolo-ai-regenerate-btn {
                border: 0;
                padding: 0;
                &:hover,
                &:focus {
                    border: 0;
                    box-shadow: none;
                }
            }
        }

        .zolo-ai-footer-btn {
            display: flex;
            align-items: center;
            gap: 10px;
        }
        .components-button {
            border-radius: 6px;
            &.insert-btn {
                background: var(--zoloblocks-brand-color);
                color: #fff;
                border-color: var(--zoloblocks-brand-color);
                &:hover,
                &:focus {
                    color: #fff;
                    background: var(--zoloblocks-brand-color-hover);
                    border-color: var(--zoloblocks-brand-color);
                    box-shadow: 0 0 0 1px var(--zoloblocks-brand-color);
                }
            }
            &.generate-btn {
                margin-left: auto;
            }
        }
    }
    .zolo-ai-error {
        background: rgba(255, 163, 163, 0.149);
        padding: 20px;
        border-radius: 6px;
        margin-bottom: 20px;
        color: #f00;
    }
}
